import React from 'react';
import './Subtitle.css';


const Subtitle = ({info}) => {


    return(
        <div className='subtitle--wrapper' id='subtitleWrapper'>
            <div className='subtitle--container'>
                <div className='subtitle--audio'>
                    <div className='subtitle--header'>Audio</div>
                    <div className='subtitle--display'>
                        <div className='subtitle--over'>
                            <div className='subtitle--option'>Portugues</div>
                            <div className='subtitle--option'>Ingles</div>
                        </div>
                    </div>
                </div>

                <div className='subtitle--text'>
                    <div className='subtitle--header'>Subtitle</div>
                    <div className='subtitle--display'>
                        <div className='subtitle--over'>
                            <div className='subtitle--option'>Portugues</div>
                            <div className='subtitle--option'>Ingles</div>
                            <div className='subtitle--option'>Off</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}


export default Subtitle;