import React, { useEffect, useState } from 'react';
import './Quality.css';


const Quality = ({info}) => {

    const [video, setVideo] = useState(document.getElementById('watchVideo'));

    const changeQuality = (e) => {
        console.log(e.target.dataset.hei)
        console.log(video.videoWidth)
    }

    useEffect(() => {
        setVideo(document.getElementById('watchVideo'))
    },[])

    return (
        <div className='quality--wrapper' id='qualityWrapper'>
            <div className='quality--container'>
                <div className='quality--header'>Qualidade</div>
                <div className='quality--display'>
                    <div className='quality--over'>
                        <div className='quality--option' data-wid={3860} data-hei={2160} onClick={(e) => changeQuality(e)}>Muito Alta 4K</div>
                        <div className='quality--option' data-wid={1920} data-hei={1080} onClick={(e) => changeQuality(e)}>Alta</div>
                        <div className='quality--option' data-wid={1280} data-hei={720} onClick={(e) => changeQuality(e)}>Media</div>
                        <div className='quality--option' data-wid={858} data-hei={480} onClick={(e) => changeQuality(e)}>Baixa</div>
                        <div className='quality--option' data-wid={480} data-hei={360} onClick={(e) => changeQuality(e)}>Muito Baixa</div>
                    </div>
                </div>
            </div>
        </div>
    )
}


export default Quality;