
import './App.css';

import { BrowserRouter, HashRouter as Router, Switch, Route } from 'react-router-dom';
import {GlobalContext, global} from './modules/Ws-Client/Global-Context';

import Web from './pages/Web/Web';
import Main from './pages/Main/Main';
import Login from './pages/Login/Login';
import Register from './pages/Register/Register'
import Watch from "./pages/Watch/Watch";

function App() {
  return (
    <BrowserRouter basename="/streamo">
      <Switch>
          <GlobalContext.Provider value={global}>
              <Route path= "/" exact={true} component={Main} />
              <Route path= "/main" exact={true} component={Main} />
              <Route path= "/web" exact={true} component={Web} />
              <Route path= "/login" exact={true} component={Login} />
              <Route path= '/register' exact={true} component={Register} />
              <Route path= '/watch' exact={true} component={Watch} />
              <Route path= '/streamo/watch' exact={true} component={Watch} />
          </GlobalContext.Provider>    
      </Switch>
  </BrowserRouter>
  );
}

export default App;
